.Portfolio {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
    margin-top: 5rem;
}

.Portfolio span:nth-of-type(1) {
    color: #242D49;
    font-size: 2rem;
    font-weight: bold;
}

.Portfolio span:nth-of-type(2) {
    color: #FCA61F;
    font-size: 2.5rem;
    font-weight: bold;
}

.Portfolio .swiper {
    overflow: visible !important;
}

.Portfolio-slider {
    margin-top: 3rem;
    width: 100%;
}

.Portfolio-slider .swiper-slide {
    width: 20rem;
}

.Portfolio img {
    width: 19rem;
    filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
    border-radius: 19px;
}